.contact {
    width: 100%;
    height: 90vh;
    display: flex;
  }
  
  .contact .leftSide {
    
    height: 100%;
    flex: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .contact .rightSide {
    height: 100%;
    flex: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .contact .leftSide h1 {
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
      "Lucida Sans", Arial, sans-serif;
    font-weight: 10;
    font-size: 50px;
    margin-left: 30px;
  }
  
  form {
      display: flex;
      flex-direction: column;
      width: auto;
      height: auto;
      padding: 30px;
    }
    
  form input {
      height: 40px;
      width: 80%;
      border: none;
      border-bottom: 1px solid #121619;
      color: black;
  }
  
  input::placeholder {
    font-weight: bold;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  form input:focus {
    outline: none;
  }
  
  form label {
    margin-top: 15px;
    color: grey;
  }
  
  form button {
    margin-top: 40px;
    width: 140px;
    height: 40px;
    border: none;
    background-color: #121619;
    color: whitesmoke;
    font-size: 15px;
    cursor: pointer;
  }
  
  .map-container {
    width: 100%;
    height: 400px;
    position: relative;
  }
  
  #map {
    width: 100%;
    height: 100%;
  }
  
  .map-overlay {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1;
    background-color: white;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  }
  
  .map-overlay label {
    display: block;
    margin-bottom: 5px;
  }
  
  .map-overlay input {
    width: 100%;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 5px;
    font-size: 14px;
    margin-bottom: 10px;
  }
  
  .map-overlay button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 3px;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
  }
  
  .map-overlay button:hover {
    background-color: #0062cc;
  }
  